import { defineStore } from 'pinia';
import { ref, watch } from 'vue';
import { getScriptIdSafe } from './utils';

// 
export type TaskType = 'ui_generate' | 'ui_modify' | 'character_generate' | 'worldbook_generate' | 'anti_cliche';

// 
export type TaskStatus = 'pending' | 'running' | 'completed' | 'failed' | 'cancelled';

// 
export interface Task {
  id: string;
  type: TaskType;
  title: string;
  status: TaskStatus;
  progress: number;
  message: string;
  details: string[];
  startTime: number;
  endTime?: number;
  result?: any;
  error?: string;
}

//  localStorage 
const loadTasksFromVariables = (): Task[] => {
  try {
    const scriptId = getScriptIdSafe();
    if (!scriptId) return [];

    const storageKey = `${scriptId}_tasks`;
    const savedDataString = localStorage.getItem(storageKey);

    if (savedDataString) {
      try {
        const savedTasks = JSON.parse(savedDataString);
        if (Array.isArray(savedTasks)) {
          console.log(' []  localStorage :', savedTasks.length);
          return savedTasks;
        }
      } catch (parseError) {
        console.error(' [] :', parseError);
      }
    }
  } catch (error) {
    console.error(' [] :', error);
  }
  return [];
};

//  localStorage
const saveTasksToVariables = (tasks: Task[]) => {
  try {
    const scriptId = getScriptIdSafe();
    if (!scriptId) return;

    // 50
    const tasksToSave = tasks.slice(0, 50);

    const storageKey = `${scriptId}_tasks`;
    localStorage.setItem(storageKey, JSON.stringify(tasksToSave));
    console.log(' []  localStorage:', tasksToSave.length);
  } catch (error) {
    console.error(' [] :', error);
  }
};

export const useTaskStore = defineStore('tasks', () => {
  const tasks = ref<Task[]>(loadTasksFromVariables());

  // 
  watch(
    tasks,
    newTasks => {
      saveTasksToVariables(newTasks);
    },
    { deep: true },
  );

  // 
  const createTask = (type: TaskType, title: string): string => {
    const id = `task_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
    const task: Task = {
      id,
      type,
      title,
      status: 'pending',
      progress: 0,
      message: '...',
      details: [],
      startTime: Date.now(),
    };
    tasks.value.unshift(task); // 
    console.log(' [] :', task.id, task.title);
    return id;
  };

  // 
  const getTask = (id: string): Task | undefined => {
    return tasks.value.find(t => t.id === id);
  };

  // 
  const updateTaskProgress = (id: string, progress: number, message: string) => {
    const task = getTask(id);
    if (task) {
      task.progress = progress;
      task.message = message;
      if (task.status === 'pending') {
        task.status = 'running';
      }
    }
  };

  // 
  const addTaskDetail = (id: string, detail: string) => {
    const task = getTask(id);
    if (task) {
      task.details.push(detail);
    }
  };

  // 
  const completeTask = (id: string, result?: any) => {
    const task = getTask(id);
    if (task) {
      task.status = 'completed';
      task.progress = 100;
      task.message = '';
      task.endTime = Date.now();
      task.result = result;
      console.log(' [] :', task.id, `: ${((task.endTime - task.startTime) / 1000).toFixed(1)}s`);
    }
  };

  // 
  const failTask = (id: string, error: string) => {
    const task = getTask(id);
    if (task) {
      task.status = 'failed';
      task.message = '';
      task.endTime = Date.now();
      task.error = error;
      console.error(' [] :', task.id, error);
    }
  };

  // 
  const cancelTask = (id: string) => {
    const task = getTask(id);
    if (task) {
      task.status = 'cancelled';
      task.message = '';
      task.endTime = Date.now();
      console.log(' [] :', task.id);
    }
  };

  // 
  const removeTask = (id: string) => {
    const index = tasks.value.findIndex(t => t.id === id);
    if (index !== -1) {
      tasks.value.splice(index, 1);
      console.log(' [] :', id);
    }
  };

  // 
  const clearCompletedTasks = () => {
    const beforeCount = tasks.value.length;
    tasks.value = tasks.value.filter(t => t.status === 'running' || t.status === 'pending');
    console.log(` [] : ${beforeCount} -> ${tasks.value.length}`);
  };

  // 
  const clearAllTasks = () => {
    tasks.value = [];
    console.log(' [] ');
  };

  // 
  const runningTaskCount = () => {
    return tasks.value.filter(t => t.status === 'running').length;
  };

  return {
    tasks,
    createTask,
    getTask,
    updateTaskProgress,
    addTaskDetail,
    completeTask,
    failTask,
    cancelTask,
    removeTask,
    clearCompletedTasks,
    clearAllTasks,
    runningTaskCount,
  };
});
