import { klona } from 'klona';
import { defineStore } from 'pinia';
import { ref, watch } from 'vue';
import { getChatIdSafe, getScriptIdSafe } from './utils';

//  zod
let z: any;
try {
  z = require('zod');
} catch (e) {
  console.warn('zod ');
  // 
  z = {
    object: (obj: any) => ({
      parse: (data: any) => data,
      default: (def: any) => ({ parse: (data: any) => data || def }),
    }),
    string: () => ({ default: (def: string) => ({ parse: (data: any) => data || def }) }),
    number: () => ({ default: (def: number) => ({ parse: (data: any) => data || def }) }),
    boolean: () => ({ default: (def: boolean) => ({ parse: (data: any) => data || def }) }),
    array: (schema: any) => ({
      default: (def: any[]) => ({ parse: (data: any) => (Array.isArray(data) ? data : def) }),
    }),
  };
}

const Settings = z
  .object({
    api_provider: z.string().default('openai'), // 'openai' | 'gemini'
    api_endpoint: z.string().default('https://api.openai.com/v1'), // base URL
    api_key: z.string().default(''),
    model: z.string().default('gpt-4o-mini'),
    max_tokens: z.number().default(4000),
    temperature: z.number().default(0.7),
    top_p: z.number().default(1.0), //  (0-1)
    presence_penalty: z.number().default(0.0), //  (-2.0 to 2.0)
    frequency_penalty: z.number().default(0.0), //  (-2.0 to 2.0)
    auto_summarize_enabled: z.boolean().default(false),
    summarize_interval: z.number().default(50), // 
    start_message_id: z.number().default(0), // 
    end_message_id: z.number().default(0), // 
    table_start_message_id: z.number().default(0), // 
    table_end_message_id: z.number().default(0), // 
    summary_history: z
      .array(
        z.object({
          start_id: z.number(),
          end_id: z.number(),
          content: z.string(),
        }),
      )
      .default([]),
  })
  .default({});

/**
 *  API  URL
 *  base URL /v1/chat/completions
 */
export function normalizeApiEndpoint(endpoint: string, path: string = '/chat/completions'): string {
  if (!endpoint || endpoint.trim() === '') {
    throw new Error('API ');
  }

  const trimmed = endpoint.trim();

  //  /chat/completions  /models
  if (trimmed.includes('/chat/completions') || trimmed.includes('/models')) {
    return trimmed;
  }

  // 
  let baseUrl = trimmed.endsWith('/') ? trimmed.slice(0, -1) : trimmed;

  //  URL  /v1 /v1
  if (!baseUrl.endsWith('/v1')) {
    baseUrl = baseUrl + '/v1';
  }

  // 
  return baseUrl + path;
}

// key
const SETTINGS_GLOBAL_KEY = 'maomao_tool_settings';

export const useSettingsStore = defineStore('settings', () => {
  //  localStorageAPI
  const isLocalDebug = () => {
    //  getVariables  localStorage
    return true; //  localStorage
  };

  //  -  localStorage
  const initSettings = () => {
    console.log('  localStorage ');
    try {
      const saved = localStorage.getItem('tavern_helper_settings');
      if (saved) {
        return ref(Settings.parse(JSON.parse(saved)));
      }
    } catch (e) {
      console.warn(' localStorage :', e);
    }
    return ref(Settings.parse({}));
  };

  const settings = initSettings();

  //  - localStorage
  const saveImmediately = (new_settings: any) => {
    try {
      console.log('  localStorage:', klona(new_settings));
      localStorage.setItem('tavern_helper_settings', JSON.stringify(klona(new_settings)));
      console.log('  localStorage');
    } catch (e) {
      console.error('  localStorage :', e);
      window.toastr?.error(': ' + (e as Error).message);
    }
  };

  // 
  let saveTimeout: number | null = null;
  const debouncedSave = (new_settings: any) => {
    if (saveTimeout) {
      clearTimeout(saveTimeout);
    }
    saveTimeout = setTimeout(() => {
      saveImmediately(new_settings);
    }, 300); // 300ms 
  };

  watch(settings, debouncedSave, { immediate: false, deep: true });

  // 
  if (typeof window !== 'undefined') {
    window.addEventListener('pagehide', () => {
      console.log(' ');
      saveImmediately(settings.value);
    });
  }

  //  - localStorage
  const saveSettings = () => {
    try {
      console.log('  localStorage:', klona(settings.value));
      localStorage.setItem('tavern_helper_settings', JSON.stringify(klona(settings.value)));
      window.toastr?.success('localStorage');
      return true;
    } catch (e) {
      console.error('  localStorage :', e);
      window.toastr?.error(': ' + (e as Error).message);
      return false;
    }
  };

  //  - localStorage
  const reloadSettings = () => {
    try {
      const saved = localStorage.getItem('tavern_helper_settings');
      if (saved) {
        settings.value = Settings.parse(JSON.parse(saved));
        console.log('  localStorage :', settings.value);
        window.toastr?.success('');
        return true;
      }
      console.warn('');
      window.toastr?.warning('');
      return false;
    } catch (e) {
      console.error(' :', e);
      window.toastr?.error(': ' + (e as Error).message);
      return false;
    }
  };

  return {
    settings,
    saveSettings,
    reloadSettings,
  };
});

//  Store -  localStorage
export const useSummaryHistoryStore = defineStore('summaryHistory', () => {
  const addSummary = (start_id: number, end_id: number, content: string) => {
    try {
      //  ID
      const chat_id = getChatIdSafe();
      if (!chat_id) {
        console.error(' ID');
        window.toastr.error('');
        return;
      }

      console.log(':', chat_id);

      //  localStorage 
      const scriptId = getScriptIdSafe();
      const storageKey = `${scriptId}_summary_history_${chat_id}`;
      let chatHistory: Array<{ start_id: number; end_id: number; content: string }> = [];

      try {
        const savedData = localStorage.getItem(storageKey);
        if (savedData) {
          chatHistory = JSON.parse(savedData);
          if (!Array.isArray(chatHistory)) {
            chatHistory = [];
          }
        }
      } catch (e) {
        console.warn(':', e);
      }

      // 
      chatHistory.unshift({
        start_id,
        end_id,
        content,
      });

      //  50 
      if (chatHistory.length > 50) {
        chatHistory = chatHistory.slice(0, 50);
      }

      //  localStorage
      localStorage.setItem(storageKey, JSON.stringify(chatHistory));

      console.log(' localStorage:', chat_id);
    } catch (error) {
      console.error(':', error);
      window.toastr.error(': ' + (error as Error).message);
    }
  };

  const getSummaryHistory = (): Array<{ start_id: number; end_id: number; content: string }> => {
    try {
      const chat_id = getChatIdSafe();
      if (!chat_id) {
        return [];
      }

      //  localStorage 
      const scriptId = getScriptIdSafe();
      const storageKey = `${scriptId}_summary_history_${chat_id}`;
      const savedData = localStorage.getItem(storageKey);

      if (savedData) {
        const history = JSON.parse(savedData);
        return Array.isArray(history) ? history : [];
      }
      return [];
    } catch (e) {
      console.warn(':', e);
      return [];
    }
  };

  return {
    addSummary,
    getSummaryHistory,
  };
});
